___CSS_LOADER_EXPORT___.push([module.id, `/* Notes css */

.notes-wrapper-wrapper {
  height: 100%;
  width: 20vw;
  max-width: 60vw;
  background: linear-gradient(
    0deg,
    rgba(53, 53, 53, 1) 25%,
    rgba(135, 206, 235, 1) 70%,
    rgba(210, 215, 223, 1) 95%
  );
  border-left: 10px solid transparent;
  margin-left: -10px; /* Very specific settings for dragging */
}

.notes-wrapper {
  height: 100%;
  touch-action: none;
  position: relative;
  min-width: 1px;
}

.top-notes,
.bottom-notes {
  width: 100%;
  flex-basis: 50%;
  flex-grow: 1;
  display: flex;
}

.top-notes {
  height: 100%;
}

.top-notes:has(+ :not(.hidden)) {
  height: 50%;
}

.bottom-notes {
  height: 50%;
}

.bottom-notes .hidden {
  height: 0;
}

.bottom-notes {
  border-top: 1px solid var(--color-jet);
}

.top-notes *:last-child,
.bottom-notes *:last-child {
  border-left: 1px solid var(--color-jet);
}

.notes {
  flex-basis: 50%;
  flex-grow: 1;
  padding: 10px;

  display: inline-block;
  white-space: nowrap;
  text-wrap: wrap;
  overflow: auto;
  overflow-wrap: break-word;
  font-family: "NotoSansMono", "sans-serif";

  background-image: linear-gradient(
    to top,
    var(--notes-line-color) 0.1em,
    var(--color-gray) 0.1em
  );
  background-size: 100% 1.4em;
  background-attachment: local;
  padding-top: 0.2rem;
  line-height: 1.4;
  border-left: 1px solid var(--color-jet);
}

.notes:has(+ .second-notes:not(.hidden)) {
  border-right: 1px solid var(--color-jet);
}

.notes:has(+ .second-notes.hidden),
.second-notes {
  padding-right: 22px;
}

.fourth-notes:focus,
.third-notes:focus,
.second-notes:focus,
.notes:focus {
  outline: none;
}

.split-notes-btn {
  position: absolute;
  top: 2px;
  right: 2px;
}

.split-notes-hor-btn {
  position: absolute;
  top: 22px;
  right: 2px;
}

.bad-resize-msg {
  position: absolute;
  bottom: -50px;
}
