___CSS_LOADER_EXPORT___.push([module.id, `/* pop ups */

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
}

.form {
  background-color: var(--color-gray);
  padding: 20px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  font-size: 24px;
  max-height: 80vh;
  max-width: 90vw;
}

.max-width-50 {
  max-width: 50vw;
}

form div.title {
  font-size: 36px;
  margin-bottom: 24px;
  text-align: center;
  word-break: break-all;
}

.form label {
  margin-top: 10px;
  word-break: break-all;
}

.form input {
  padding: 4px;
  line-height: 1.4em;
  font-size: inherit;
  max-width: 80vw;
}

form input[name="url"] {
  width: 40ch;
}

.even-list {
  align-self: center;
  width: fit-content;
  & * {
    width: 100%;
  }
}

.center-text {
  text-align: center;
}

.manual-choices-group {
  display: flex;
  flex-direction: column;
}
