  --color-jet: #353535;
  --color-gray: #d2d7df;
  --color-dark-gray: hsl(217, 17%, 70%);
  --color-silver: #bdbbb0;
  --color-light-jet: hsl(0, 0%, 40%);
  --color-green: #cefb89;
  --color-red: #ed9190;
  --color-blue: #87ceeb;
  --color-orange: #edc090;
  --page-bg-color: var(--color-gray, white);
  --footer-height: 50px;

  --notes-line-color: #85c1e9;
}

@font-face {
  font-family: "special-elite";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("truetype");
}

@font-face {
  font-family: "anonymous pro";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("truetype");
}

@font-face {
  font-family: "NotoSansMono";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format("truetype");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "special-elite";
}

input {
  width: 100%;
  font-family: "anonymous pro", "sans-serif";
}

.hidden {
  display: none !important;
}

button {
  font-size: 1.2rem;
  color: #87ceeb;
  background-color: var(--color-jet);
  border-radius: 10px;
  border: 1px solid var(--color-gray);
  padding: 6px 12px;
}

button:hover {
  cursor: pointer;
  color: white;
}

ul,
li {
  list-style: none;
}

.gray {
  color: var(--color-gray) !important;
}

.red {
  color: var(--color-red) !important;
}

.green {
  color: var(--color-green) !important;
}

.orange {
  color: var(--color-orange) !important;
}

svg {
  min-width: 1.5em;
  min-height: 1.5em;
  width: 1.5em;
  height: 1.5em;
  cursor: pointer;
  stroke: var(--color-jet);
}

.hover {
  width: fit-content;
}

.main-container {
  background: var(--page-bg-color);
  display: flex;
  flex-direction: column;
  height: 100dvh;
  height: 100vh;
  gap: 1px;
}

.main {
  display: flex;
  flex: 1;
  overflow: hidden;
  position: relative;
}

.top-margin {
  margin-top: 10px;
}

/* Required Text */
label[for="serial"],
label[for="url"],
label[for="name"] {
  position: relative; /* Do not remove: Needed for edgework ":after" */
}
label[for="url"]:after,
label[for="name"]:after {
  content: "required";
  color: var(--color-red);
  font-size: 0.5em;
}

.form-btn-group {
  display: flex;
  justify-content: center;
  gap: 4px;
  margin-top: 8px;
}

.column-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
}

.text-wrap {
  white-space: pre-line;
}

.underline {
  text-decoration: underline;
}
